<template>
    <div class="header">
        <div class="flex-row-lg align-items-start header__wrapper">
            <div class="flex-row-lg header__logo header__logo-row margin-right"
                 v-bind:class="logoMargin">
                <img src="./../assets/img/logo.png" alt="logo" class="header__logo-img">
                <div class="header__logo-text" v-if="!isQuiz">Крупнейший дистрибьютор кровельных материалов в Беларуси
                </div>
            </div>
            <div class="header__left-container align-items-start flex-row" v-if="!isQuiz">
                <div class="header__location header__item flex-row" style="margin-right: 32px;">
                    <img src="./../assets/img/location.png" alt="location"
                         class="header__location-icon header__icon">
                    <div class="header__location-text">г. Минск, ул. Притыцкого<br>62/2 офис 711</div>
                </div>
                <div class="header__pdf header__item flex-column" style="margin-right: 25px">
                    <div class="flex-row">
                        <img src="./../assets/img/pdf.png" alt="pdf" class="header__pdf-icon">
                        <a href="img/pdf.png" class="header__pdf--text accent">Скачать ТОП 5 ошибок при
                            выборе<br> кровельных материалов
                        </a>
                    </div>
                    <div class="header__pdf-under">PDF 1,5 Mb</div>
                </div>
                <div class="header__phone header__item flex-row">
                    <img src="./../assets/img/tel.png" alt="location" class="header__phone-icon header__icon">
                    <div class="flex-column">
                        <a href="tel:+375447405995" class="header__phone-text">+375 (44) 740-59-95</a>
                        <a v-on:click="show" class="header__phone-callback-link accent">Заказать обратный звонок</a>
                    </div>
                </div>
            </div>
            <div class="header__quiz-text" v-if="isQuiz && !isThanksPage"><span class="accent">Калькулятор</span> стоимости кровли</div>
            <div class="quiz__close-wrap" v-if="isQuiz">
                <img src="../assets/img/close.png" alt="" class="header__quiz-close" v-on:click="closeQuiz">
            </div>
        </div>
        <modal :adaptive="true" :height="430" name="modal">
            <div class="modal__content">
                <img src="../assets/img/close.png" alt="x" class="header__quiz-close modal__content-close" v-on:click="hide">
                <h3 class="modal__title">Заказать обратный звонок</h3>
                <input v-model="username" placeholder="Ваше имя" type="text" class="modal__name-input">
                <input v-model="phone" type="text" placeholder="Ваш телефон" class="modal__phone-input">
                <button class="button main__button modal__button" v-on:click="sendData" :disabled="username.length < 2 || phone.length < 5">Отправить</button>
            </div>
        </modal>
    </div>
</template>

<script>
    const axios = require('axios').default;
    export default {
        name: 'Header',
        props: {
            isQuiz: Boolean,
            isThanks: Boolean,
        },
        data() {
            return {
                username: "",
                phone: "",
            }
        },
        methods: {
            sendData() {
                this.processData(this);
            },
            processData(context) {
                let obj = {};
                obj["Имя"] = this.username;
                obj["Телефон"] = this.phone;
                axios.post('mail.php', obj ).then(function (response) {
                    console.log(response);
                    context.$emit('thanks');
                    context.isThanksPage = true;
                }).catch(function (error) {
                        console.log(error);
                    });
                context.hide();
            },
            closeQuiz() {
                this.$emit('closeQuiz');
            },
            show() {
                this.$modal.show('modal');
            },
            hide() {
                this.$modal.hide('modal');
            }
        },
        computed: {
            logoMargin: function () {
                return {
                    'margin-center': this.isQuiz && window.innerWidth < 840,
                }
            },
            isThanksPage: function () {
                return this.isThanks;
            },
        }
    }
</script>

