<script>
    import smoothscroll from 'smoothscroll-polyfill';
    smoothscroll.polyfill();
    const axios = require('axios').default;
    export default {
        name: "Quiz",
        props: {
            quiz: Array,
            thanks: Boolean,
        },
        computed: {
            progress: function () {
                return this ? this.isFinal ? this.progressWidth : this.progressWidth / this.quiz.length * (this.currentSlide + 1) : 0
            },
        },
        mounted() {
            this.progressWidth = this.$refs.progress.clientWidth;
        },
        data() {
            return {
                isThankPage: this.thanks,
                isFinal: this.thanks,
                currentSlide: 0,
                progressWidth: 0,
                currentAnswer: "",
                sliderValue: 10,
                formData: {},
                phone: "",
                showOverlay: false,
            }
        },
        methods: {
            collectData() {
                let name = this.isFinal ? "Способ связи" : this.quiz[this.currentSlide].title;
                this.formData[name] = this.currentAnswer;
                if (this.sliderValue > 0 && this.formData["Площадь"] === undefined) {
                    this.formData["Площадь"] = this.sliderValue;
                }
                if (this.isFinal) {
                    this.formData["Номер телефона"] = this.phone;
                }
                this.currentAnswer = "";
            },
            sendData(context) {
                axios.post('mail.php', this.formData).then(function (response) {
                    console.log(response);
                    context.isThankPage = true;
                    context.$emit('thanks');
                })
                    .catch(function (error) {
                        console.log(error);
                    });
            },
            nextStep() {
                this.collectData();
                if (this.isFinal) {
                    this.sendData(this);
                } else if (this.currentSlide + 1 < this.quiz.length) {
                    this.currentSlide += 1;
                } else {
                    this.isFinal = true;
                    this.quiz[this.currentSlide].hint = this.formData["Ваш подарок:"];
                }
                this.showStepOverlay();
            },
            showStepOverlay() {
                if(window.innerWidth < 840) {
                    document.getElementById('quiz').scrollIntoView({behavior: "smooth"});
                }
                this.showOverlay = true;
                setTimeout(() => {
                    this.showOverlay = false;
                }, 550);
            }
        }

    }
</script>

